export default {
	data() {
		return {
			
		};
	},
	props: {
		show_space: Boolean,
		goods_infor: Object,
		goods_data: Object
	},
	methods: {
		hide_popup_spec() { //不可以直接修改 props 中的数据,需要通过子级注册事件 父级执行事件。
			this.$emit('hide_popup_spec');
		},
		select_spec(spec) { //选择尺码
			this.$emit('select_spec',spec);
		}
	}
}
